.App {
  text-align: center;
}
.login-register {
  width: 30%;
  border: 2px solid #768084;
  color: #fff;
  padding: 10px;
  margin: 0 auto;
}
input {
  /*color: white!important;*/
  border: 2px solid #768084!important;
  background-color: #181a1b !important;
  color: #c7ced0 !important;
}
textarea {
  /*color: white!important;*/
  border: 2px solid #768084!important;
  color: #C7CED0!important;
  background-color: #181a1b!important;
}
.card{
  /*width: 30%;*/
  border: 2px solid #768084;
  /*margin: 5px 5px 20px;*/
  background-color: #181a1b;
  color: #c7ced0;
}
a{
  color: #c7ced0;
  text-decoration: none;
}
a:hover{
  color: #c7ced0;
  text-decoration: underline;
}
.card-header{
  background-color: #8e0000;
  height: 100%;
  color: #fff;
}
.card-footer{
  /*background-color: #8e0000!important;*/
}
button{
  margin-top: 10px;
  margin-bottom: 20px;
}
img {
  width: 100%;
}
.profile-image{
  height: 20%;
  width: 30%;
  border-radius: 100px;
  margin-bottom: 20vw;
}
 section {
  width: 100%;
  height: 50%;
  margin-right: 200px;
}
.left-boarder {
  background-color: #8e0000;
  width: 40vw;
  height: 100vh;
}
.right-boarder {
  height: 100vh;
  width: 50vw;
  background-color: #c7ced0;
  border-top: 4px solid #c7ced0;
  border-right: 4px solid #c7ced0;
  border-bottom: 4px solid #c7ced0;
  color: #181A1B;
}
.test{
  display: flex;
}
.btn-danger{
  border: none;
  background-color: #ab0303;
}
.btn-primary{
  border: none;
  background-color: #1b1b7c;
}
.btn-success{
  border: none;
  background-color: #768084;
}
.btn-success:disabled{
  background-color: #768084;
}
.btn-success:hover{
  border: none;
  background-color: #5b6265;
}
.btn-success:focus{
   background-color: #5b6265;
}
.about-text{
  margin-top: 15%;
}
.nav{
  padding: 20px;
}
.planet-width{
  /*max-width: 1000px;*/
}
